.main {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  padding: 10px;
}

.search {
  border: 0px solid rgb(162, 157, 157);
  font-size: 20px;
}

.searchIcon {
  width: 20px;
  height: 20px;
}

.content {
  display: flex;
  align-items: center;
  gap:10px;
}

.searchDiv {
  display: flex;
  justify-content: space-around;
  width: 600px;
  align-items: center;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 5px;
  border-radius: 9px;
}

.location {
  width: 20px;
  height: 20px;
}

.locationDiv {
  display: flex;
  align-items: center;
  border-right: 1px solid rgb(176, 171, 171);
  height: 45px;
  padding-right: 25px;
}

.loginDiv {
  display: flex;
  gap: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.logo {
  font-size: 30px;
  font-style: italic;
  color: black;
  font-weight: 900;
}

.logo:hover{
  text-decoration: none;
}

.search:focus {
  outline: none;
  border: none;
}

.loginText{
  font-weight: 300;
}

@media all and (max-width:412px){
  .main {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
  }
  
  .search {
    border: 0px solid rgb(162, 157, 157);
    font-size: 10px;
  }
  
  .searchIcon {
    width: 10px;
    height: 10px;
  }
  
  .content {
    display: flex;
    align-items: center;
    gap:5px;
  }
  
  .searchDiv {
    display: flex;
    justify-content: space-around;
    width: 250px;
    align-items: center;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 10px;
    border-radius: 9px;
    margin-left: -40px;
  }
  
  .location {
    width: 10px;
    height: 10px;
  }
  
  .locationDiv {
    display: flex;
    align-items: center;
    border-right: 1px solid rgb(176, 171, 171);
    height: 25px;
    padding-right: 5px;
  }
  
  .loginDiv {
    display: none;
  }
  
  .logo {
    font-size: 10px;
  }
  
  .search:focus {
    outline: none;
    border: none;
  }
  .items{
    margin-left: -40px;
    font-size: 10px;
    display: flex;
    gap:10px;
    align-items: center;
  }

  .item{
    margin-top: 12px;
  }
}